.wrapper {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stop-watch {
  border: 4px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stop-watch__dot {
  z-index: 1;
  background-color: #3aa2b4;
  border-radius: 50%;
  width: 3.2vmin;
  height: 3.2vmin;
  position: absolute;
  top: 38.4vmin;
  left: 38.4vmin;
}

@keyframes clock-hand-rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stop-watch__big-hand {
  transform-origin: bottom;
  background-color: #4492a0;
  width: 1.6vmin;
  height: 40vmin;
  animation: 5s linear infinite clock-hand-rotate;
  position: absolute;
  top: 0;
  right: 39.2vmin;
}

.stop-watch__small-hand {
  transform-origin: 0;
  background-color: #556188;
  width: 26.6667vmin;
  height: 2.4vmin;
  animation: 60s linear infinite clock-hand-rotate;
  position: absolute;
  top: 38.8vmin;
  left: 40vmin;
}

